import React from 'react';
import { IoChevronForwardOutline } from "react-icons/io5";

export function MxButton({ name, onPress, style, icon = false }) {


    return (
        <button
            className="mxButton"
            style={{ ...style, alignItems: "center", }}
            onClick={onPress}
        >

            {name} {icon ? <IoChevronForwardOutline style={{ marginBottom: "2.5px" }} /> : null}


        </button>
    );
}

export function MxButtonGroup({ name1, name2, onPress2, style1, style2, onPress1, icon1 = false }) {

    return (
        <>
            <button
                className="mxButton"
                style={style1}
                onClick={onPress1}
            >
                {name1} {icon1 ? <IoChevronForwardOutline style={{ marginBottom: "2.5px" }} /> : null}
            </button>
            <button
                className="mxButton"
                id="button-2"
                style={style2}
                onClick={onPress2}
            >
                {name2}
            </button>
        </>
    );
}
