import React from 'react'
import { BsChevronDown, BsChevronUp } from 'react-icons/bs';
import cssVar from "views/mobileView/styles";

export default function ReadMoreText({ text, onViewChange = () => { } }) {

  const [isReadMore, setIsReadMore] = React.useState(true);

  React.useEffect(() => {
    onViewChange(!isReadMore)
  },[isReadMore])

  if (!text) {
    return ''
  }
  if (text.length < 100) {
    return text
  }
  else return <>
    {isReadMore ? text?.slice(0, 80) + "..." : text}
    <span onClick={(e) => { e.stopPropagation(); setIsReadMore(!isReadMore) }} style={{ color: cssVar.getPropertyValue('--darkGreen'), fontWeight: "500", whiteSpace: "nowrap" }}>
      {isReadMore ? " Read More " : " Read Less "}{isReadMore ? <BsChevronDown size={12} /> : <BsChevronUp size={12} />}
    </span>
  </>
} 