import db from "libs/db";
import React from "react";
import Spinner from "react-bootstrap/Spinner";
import { isMobileOnly } from "react-device-detect";
import { FaChartLine } from "react-icons/fa";
import { FiSearch } from 'react-icons/fi';
import InfiniteScroll from "react-infinite-scroll-component";
import { toast } from "react-toastify";
import AdminServices from "services/api/AdminServices";
import { CurrencyFormat } from "views/mobileView/components/atoms/CurrencyFormat";
import MintboxIcon from 'views/mobileView/components/atoms/MintboxIcon';
import { MxButton, MxButtonGroup } from "views/mobileView/components/atoms/MxButton";
import ReadMoreText from "views/mobileView/components/atoms/ReadMoreText";
import { RiskFormat } from "views/mobileView/components/atoms/RiskFunctions";
import MxDialog from "views/mobileView/components/molecules/Cards/MxDialog";
import MarketingFooter from "views/mobileView/components/molecules/Footers/MarketingFooter";
import MarketingHeader from "views/mobileView/components/molecules/Headers/MarketingHeader";
import cssVar from 'views/mobileView/styles';



export default function AllFunds(props) {


    const [showDownload, setShowDownload] = React.useState(false);
    const [currentMint, setcurrentMint] = React.useState(0);


    const [open, setOpen] = React.useState(false)

    const handleClickToOpen = () => {
        if (isMobileOnly) {
            const win = window.open("/qr-custom/app-link", "_blank");
            win.focus();
        } else {
            setOpen(true);
        }
    }

    const handleToClose = () => {
        setOpen(false);
    };


    const [loading, setLoading] = React.useState(false)
    const [searchText, setSearchText] = React.useState('');

    const [mintFunds, setMintFunds] = React.useState([])
    const [filteredFunds, setFilteredFunds] = React.useState([])
    const [searchResult, setSearchResult] = React.useState([])
    const [data, setData] = React.useState([]) // used to display

    const [allSelected, setAllSelected] = React.useState(true)
    const [riskSelected, setRiskSelected] = React.useState('')
    const [durationSelected, setDurationSelected] = React.useState('')

    const type = props.location.state?.type ? props.location.state?.type : 3

    const riskChipset = [
        { "key": "1", "name": RiskFormat(1) },
        { "key": "2", "name": RiskFormat(2) },
        { "key": "3", "name": RiskFormat(3) },
        { "key": "4", "name": RiskFormat(4) },
        { "key": "5", "name": RiskFormat(5) },
    ]

    const durationChipset = [
        { "key": "1", "name": "1-3 year" },
        { "key": "2", "name": "3-5 year" },
        { "key": "3", "name": "5+ year" },
    ]


    React.useEffect(async () => {
        await loadData()
    }, []);

    React.useEffect(async () => {
        if (mintFunds !== []) {
            const query = props.location.state?.query ? props.location.state?.query : 'All';

            if (query !== "All") {
                setDurationSelected(query);
                setAllSelected(false)
                filterFunds(false, '', query)
            } else {
                filterFunds(true, '', '')
            }
        }
    }, [mintFunds])

    React.useEffect(async () => {
        onSearch();
    }, [filteredFunds, searchText])

    React.useEffect(async () => {
        setData(searchResult.slice(0, 25))
    }, [searchResult])

    const filterFunds = async (isAll, riskScore, durationId) => {
        if (isAll) {
            setFilteredFunds(mintFunds)
        } else {
            console.log(riskScore, durationId)
            if (riskScore !== '' && durationId !== '') {
                console.log('sdjknksjd')
                setFilteredFunds(mintFunds.filter(m => m.RiskLevel === Number(riskScore) && m.DurationId === Number(durationId)))
            } else if (riskScore !== '') {
                setFilteredFunds(mintFunds.filter(m => m.RiskLevel === Number(riskScore)))
            } else if (durationId !== '') {
                setFilteredFunds(mintFunds.filter(m => m.DurationId === Number(durationId)))
            } else {
                setFilteredFunds(mintFunds)
            }
        }
    }

    const onSearch = async () => {
        // setLoadingFilter(true)
        let res = [].concat(filteredFunds)
        if (searchText.length > 0) {
            const textArray = searchText.split(' ');
            if (type === 1) {
                res = filteredFunds.filter(fund => textArray.every(text => fund.ETFName.toLowerCase().indexOf(text.toLowerCase()) > -1))
            } else if (type === 2) {
                res = filteredFunds.filter(fund => textArray.every(text => fund.FundName.toLowerCase().indexOf(text.toLowerCase()) > -1))
            } else {
                res = filteredFunds.filter(fund => textArray.every(text => fund.MintboxName.toLowerCase().indexOf(text.toLowerCase()) > -1))
            }
        }
        console.log(res)
        setSearchResult(res)
    }

    const loadData = async () => {
        setLoading(true);
        await db.get(db.model.allMintboxes).then(async (u) => {
            if (u) {
                console.log("mint")
                setMintFunds(u);
                setLoading(false);
            } else {
                try {
                    let res3 = await AdminServices.getAllMintboxes(0).then(res3 => res3);
                    if (res3.type === true) {
                        console.log("Get All Mintbox API: ", res3.data)
                        await db.set(db.model.allMintboxes, JSON.stringify(res3.data))
                        setMintFunds(res3.data)
                        setLoading(false);
                    }
                } catch (error) {
                    setLoading(false);
                    toast.error(error);
                }
            }
        })
    }

    const loadMoreFunds = () => {
        setData(searchResult.slice(0, data.length + 10)) // set data with 10 additionalfunds
    };

    if (loading) {
        return (
            <div style={{ flex: 1 }}>
                <div style={{ flex: 1, justifyContent: 'center', alignContent: 'center' }}>
                    <Spinner
                        as="span"
                        animation="grow"
                        size="sm"
                        role="status"
                        aria-hidden="true"
                    />
                </div>
            </div>
        );
    } else {
        return (
            <div>
                <MarketingHeader props={props} />
                <section id="section1">
                    <div style={{ backgroundColor: "#285e4d" }}>
                        <div className="mxContainerData" style={{ paddingBottom: 100, height: "100%", backgroundColor: "#fff" }} >
                            <div className="mxSubContainer" style={{ width: "100%", justifyContent: "space-between" }}>
                                <div style={{ display: "flex", flexDirection: "column", minWidth: "25%", paddingTop: "1%" }}>
                                    <div style={{ display: "flex" }}>
                                        <input
                                            type="checkbox"
                                            id="checkbox"
                                            name="checkbox"
                                            checked={allSelected}
                                            onChange={() => {
                                                setAllSelected(true);
                                                setRiskSelected('');
                                                setDurationSelected('');
                                                filterFunds(true, '', '')
                                            }}
                                            className='greenCheckbox'
                                        />
                                        <label htmlFor="checkbox" style={{ flex: 1 }}>
                                            All mintboxes
                                        </label>
                                    </div>
                                    <div className='header1dark' style={{ margin: '10% 0 0 0' }}>Risk level</div>
                                    {
                                        riskChipset.map(r => {
                                            return <div className="" style={{ marginTop: "6%" }}>
                                                <input className='greenCheckbox' type="checkbox" name="risk" value={r.key} id={r.key + 'r'} checked={riskSelected === r.key}
                                                    onChange={(e) => {
                                                        if (riskSelected === r.key) {
                                                            setRiskSelected('')
                                                            filterFunds(false, '', durationSelected)
                                                        }
                                                        else {
                                                            setRiskSelected(e.target.value);
                                                            filterFunds(false, r.key, durationSelected)
                                                        }
                                                        setAllSelected(false);
                                                    }} />
                                                <label className="checkbox" htmlFor={r.key + 'r'}>{r.name}</label>
                                            </div>
                                        })
                                    }
                                    <div className='header1dark' style={{ margin: '10% 0 0 0' }}>Investment horizon</div>
                                    {
                                        durationChipset.map(r => {
                                            return <div className="" style={{ marginTop: "6%" }}>
                                                <input className='greenCheckbox' type="checkbox" name="horizon" value={r.key} id={r.key + 'h'} checked={durationSelected === r.key}
                                                    onChange={(e) => {
                                                        if (durationSelected === r.key) {
                                                            setDurationSelected('');
                                                            filterFunds(false, riskSelected, '')
                                                        } else {
                                                            setDurationSelected(e.target.value);
                                                            filterFunds(false, riskSelected, r.key)
                                                        }
                                                        setAllSelected(false)
                                                    }} />
                                                <label className="checkbox" htmlFor={r.key + 'h'}>{r.name}</label>
                                            </div>
                                        })
                                    }
                                </div>

                                <div style={{ display: "flex", flex: 1, flexDirection: "column", maxWidth: "75%", position: "sticky", top: 0 }}>
                                    <div style={{
                                        display: "flex",
                                        flexDirection: "row",
                                        justifyContent: "space-between",
                                        alignItems: "center",
                                        marginBottom: "20px"
                                    }}>
                                        <div style={{ padding: "4px", display: "flex", flexDirection: "row", alignItems: "center", border: "1px solid #E5E5E5 " + cssVar.getPropertyValue("--divider"), borderRadius: '4px', height: "35px", width: "40%", backgroundColor: "#FAFAFA" }}>
                                            <FiSearch size={20} style={{ margin: "0 4%", color: "#294C88" }} />
                                            <input
                                                placeholder={type === 1 ? 'Search by ETF name' : type === 2 ? 'Search by mutual fund name' : 'Search by mintbox name'}
                                                style={{ fontSize: 15, color: cssVar.getPropertyValue("--text"), border: "none", outline: "none", width: "100%", marginTop: 2, backgroundColor: "#FAFAFA" }} value={searchText}
                                                onChange={(e) => { setSearchText(e.target.value) }}
                                            />
                                        </div>
                                    </div>

                                    <InfiniteScroll
                                        dataLength={searchResult.length} //This is important field to render the next data
                                        next={() => loadMoreFunds()}
                                        hasMore={data.length < searchResult.length}
                                        loader={<center>
                                            <Spinner
                                                as="span"
                                                animation="grow"
                                                size="sm"
                                                role="status"
                                                aria-hidden="true" />
                                        </center>}
                                    >
                                        {
                                            data.map((mintbox, index) =>
                                                <div key={mintbox.MintboxId}
                                                    // onClick={() => props.history.push("/mintbox-description/" + mintbox.MintboxId)}
                                                    className="py-4" style={{ display: "flex", flexDirection: "row", borderBottom: "1px solid #E5E5E5", cursor: "pointer" }}>

                                                    <MintboxIcon risk={mintbox.RiskLevel} height="56px" width="55px" />
                                                    <div className="mx-4" style={{ overflow: "hidden", display: "flex", flexDirection: "column", minWidth: "30%", flex: 1 }}>
                                                        <span style={{ fontSize: "17px", color: "#333", fontWeight: "500", lineHeight: "25.5px" }}>{mintbox.MintboxName}</span>
                                                        <span className="" style={{ fontSize: "14px", width: "90%", color: "#6c6c6c", lineHeight: "21px", paddingTop: "8px", whiteSpace: 'pre-line' }}>
                                                            <ReadMoreText onViewChange={(p) => {
                                                                setShowDownload(p)
                                                                setcurrentMint(index)
                                                            }} text={mintbox.About.replaceAll(`\{\"\\n\"\}`, '\n\n')} />
                                                        </span>
                                                    </div>

                                                    <div style={{ display: "flex", flexDirection: "column" }}>
                                                        <div style={{ display: "flex", flexDirection: "row" }}>
                                                            <div style={{ display: "flex", flexDirection: "column", paddingRight: "60px", paddingLeft: "50px" }} >
                                                                <span style={{ fontSize: "14px" }}>Min SIP</span>
                                                                <span>{CurrencyFormat(mintbox.MinSIPAmount)}</span>
                                                            </div>

                                                            <div style={{ display: "flex", flexDirection: "column", paddingRight: "20px" }} >
                                                                <span style={{ fontSize: "14px" }}><FaChartLine size={20} style={{ color: "#C2DC55", paddingRight: "5px" }} />CAGR</span>
                                                                <span style={{ color: "#285e4d" }} >{parseFloat(mintbox.XIRR || 0).toFixed(2)} %</span>
                                                            </div>
                                                        </div>
                                                        {showDownload && currentMint === index ? <div style={{ display: "flex", flexDirection: "row", paddingLeft: "50px" }}>
                                                            <div style={{ flex: 1, justifyContent: "center" }}>
                                                                <MxButton
                                                                    name="Install app to invest"
                                                                    onPress={handleClickToOpen}
                                                                    style={{
                                                                        borderColor: "#F7D749",
                                                                        backgroundColor: "#F7D749",
                                                                        marginTop: 30,
                                                                        fontSize: "14px"
                                                                    }}
                                                                />
                                                                <MxDialog props={props} open={open} handleToClose={handleToClose} />
                                                            </div>
                                                        </div> : null}
                                                    </div>

                                                </div>
                                            )
                                        }
                                        {data.length === 0 ? <span className="mxTopSubHead" style={{ color: "#333", fontWeight: "400", paddingTop: "100px", fontSize: "18px", letterSpacing: "-0.36px", textAlign: "center" }}>
                                            No result found!
                                        </span> : null}
                                    </InfiniteScroll>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>



                <section>
                    <div className="mxContainerData mxDesktopHidden" style={{ backgroundImage: "linear-gradient(to right,#F5F5F5, #F5F5F5)", height: "25vh", }} >

                        <div style={{}}>
                            <span className="mxTopSubHead" style={{ color: "#333", fontWeight: "700", marginTop: 0, fontSize: "18px", letterSpacing: "-0.36px" }}>
                                Ready to invest in mintboxes and grow your money?
                            </span>
                            <div style={{}}>

                                <MxButtonGroup
                                    name1="Get started"
                                    name2="Schedule a call"
                                    style1={{
                                        borderColor: "#333",
                                        backgroundColor: "#333",
                                        color: "#fff",
                                    }}
                                    style2={{
                                        borderColor: "#6c6c6c",
                                        backgroundColor: "transparent",
                                        color: "#6c6c6c",
                                    }}
                                />

                            </div>
                        </div>

                    </div>

                    <div className="mxContainerData mxDesktopVisible" style={{ alignItems: "center", backgroundImage: "linear-gradient(to right,#1B5240, #288A6B)", paddingTop: "0px", paddingBottom: "0px", height: "30vh", }} >

                        <div style={{ flex: 1, }}>
                            <span className="mxHead" style={{ color: "#fff", textAlign: "right", float: "right" }}>
                                Ready to invest in diversified investment portfolios?
                            </span>
                        </div>
                        <div style={{ flex: 1, }}>
                            <MxButton
                                name="Download the app"
                                onPress={handleClickToOpen}
                                style={{
                                    borderColor: "#F7D749",
                                    backgroundColor: "#F7D749",
                                    marginTop: 0,
                                    marginLeft: "25%",
                                }}
                            />
                            <MxDialog props={props} open={open} handleToClose={handleToClose} />
                        </div>
                    </div>
                </section>

                <section>
                    <MarketingFooter props={props} faq={false} clickPricing="/#section6" clickFeatures="/#section5" />
                </section>

            </div>
        );
    }
}
















































